.home-container {
  height: 100vh;
  width: 100vw;
}

.main-container {
  height: 100vh;
  width: 100vw;
}

a {
  text-decoration: none;
}

.background-img {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-image: url(https://images.pexels.com/photos/1229861/pexels-photo-1229861.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(8px);
}

.arrow {
  position: relative;
  width: 3vmin;
  height: 3vmin;
  background: transparent;
  border-top: 1vmin solid white;
  border-right: 1vmin solid white;
  -webkit-box-shadow: 0 0 0 lightgray;
          box-shadow: 0 0 0 lightgray;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  padding: 30px;
}

.arrow.left {
  left: 40px;
  -webkit-transform: translate3d(0, -50%, 0) rotate(-135deg);
          transform: translate3d(0, -50%, 0) rotate(-135deg);
}

.arrow.right {
  right: 40px;
  -webkit-transform: translate3d(0, -50%, 0) rotate(45deg);
          transform: translate3d(0, -50%, 0) rotate(45deg);
}

.arrow:hover {
  border-color: orange;
  -webkit-box-shadow: 0.5vmin -0.5vmin 0 white;
          box-shadow: 0.5vmin -0.5vmin 0 white;
}

.arrow:before {
  content: '';
  -webkit-transform: translate(-40%, -60%) rotate(45deg);
          transform: translate(-40%, -60%) rotate(45deg);
  width: 200%;
  height: 200%;
}

.navbar {
  font-size: 20px;
  height: 10vh;
  background-color: var(--background) !important;
}

.navbar-text {
  -webkit-transition: 0.5s !important;
  transition: 0.5s !important;
}

.nav-link:hover {
  color: blue !important;
  font-size: 20px !important;
  cursor: pointer !important;
}

.nav-link {
  cursor: pointer !important;
}

.sidebar-slide {
  width: 250px !important;
}

.page-slide {
  margin-left: 250px;
}

.openbtn:hover {
  background-color: #444;
}

.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: #111;
  color: white;
  padding: 10px 15px;
  border: none;
  position: fixed;
  z-index: 1;
}

:root {
  --projects-back: white;
  --background: rgb(27, 15, 49);
  --light-blue: #04C3FF;
  --light-blue-shadow:  rgb(69 211 255 / 27%);
  --sea-green: #0ABEAD;
  --sea-green-dark: #0CBFAE;
  --sea-green-extra-dark: #09BEAD;
  --sea-green-shadow: rgba(11, 190, 173,0.4);
  --header-hamburger-background: rgba(11, 190, 173,0.8);
  --orange: #F57F17;
  --dark-orange: #FF5823;
  --orange-shadow: rgba(245, 127, 23,0.2);
  --white: #ffffff;
  --black: #333333;
  --disabled-btn: #09bead7a;
  --error-message:  #e44f4fd1;
  --toast-error: #e44f4ffa;
  --toast-shadow:  #9c9c9ce3;
  --toast-success: #0CBFAE;
  --loader-color: #0CBFAE
;
}
