.profile-container {
    text-align: center;
    width: 100vw;
    height: 100vh;
    color: black;
}

.profile-parent {
    width: fit-content;
    height: fit-content;
    margin: auto;
    display: flex;
    align-items: center;
    background-color: whitesmoke
}

.profile-details-name {
    padding-top: 100px;
    margin-bottom: 10rem;
    font-size: 50px;
    color: white;
    text-align: center;
    font-family: 'Fredoka One', cursive;
}

#profile-details-role {
    margin: 14px 0 24px 30px;
    width: 30rem;
    height: 15rem !important;
    text-align: left;
    font-size: 1.1rem;
}


  

.primary-btn {
    border-radius: 50px;
    padding: 14px 0;
    width: 160px;
    cursor: pointer;
    transition: 0.2s;
    color: var(--white);
    border: 2px solid linen;
    font-size: 12px;
    background-color: #1f2235;
  }


.profile-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 380px;
    width: 380px;
}

.profile-picture-background {
    height: 92%;
    width: 92%;
    background-image: url(../assets/images/peter.jpg);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

.profile-picture-background:hover {
    transform: scale(1.07);
    transition: 1s ease-out;
}



