
/* WebDev Carousel animation code 
*********************************
*/

*, *::before, *::after {
    box-sizing: border-box;
  }
  
  .projects-container {
    width: 100vw;
    height: 100vh;
    color: white;
  }

  .projects-header {
    position: relative;
    top: 100px;
    text-align: center;
    font-size: 50px;
    font-family: 'Fredoka One', cursive;
  }

  .projects-list {
    display: flex;
    height: 80vh;
    flex-flow: column;
    align-items: center;
    justify-content: center;
  }


  
  .projects-list a {
    all: unset;
    cursor: pointer;
    user-select: none;
    position: relative;
    font-size: 3rem;
    text-align: center;
    width: fit-content;
    font-family: 'Kanit', sans-serif;  
  }
  
  .projects-list a::before {  
    transform: scaleX(0);
    transform-origin: bottom right;
    border-radius: 80px;
  }
  
  .projects-list a:hover::before {
    transform: scaleX(1);
    transform-origin: bottom left;
    border-radius: 80px;
  }
  
  .projects-list a::before {
    content: " ";
    border-radius: 80px;
    display: block;
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    inset: 0 0 0 0;
    background: hsl(200 100% 80%);
    z-index: -1;
    transition: transform .3s ease;
  }

  .projects-header + ul {
    text-decoration: none;
    position: relative;
    top: 10rem;
  }
